body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing:border-box;
  margin: 0;
  padding: 0;}
html {
  font-size: 62.5%; /*usar 1rem para 10pixeles. 62.5% = 10px (en base al navegador)
  1rem = 10px
  1.6rem = 16px*/
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background:whitesmoke;}

.header-main{
  height: 10%;
  text-align: center;
  background-image: linear-gradient(rgba(20, 20, 2, 0.925), rgba(22, 36, 5, 0.5));
  /* background-image: linear-gradient(to top, transparent, rgb(192, 217, 228)), url(../images/backgBlack.jpg); */
  /* background: linear-gradient(to bottom left, rgb(1, 8, 29) 50%, rgb(24, 20, 143) 50%); */
  margin: 2rem;}

.header-main span{
  font-size: 60px;
  color: blanchedalmond;
  margin-top: 1rem;}

.header-main h1{
  color:seashell;
  font-size: 3rem;
 
  font-weight: bold;
 /* margin: 22px; */
}
.header-main p{text-align: center;
  color: blanchedalmond;
  
  font-size: 1.6rem;
  margin-top: 2px;}

  .header-main ul{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding-left: 0px;
  }

  .header-main li{
    margin-bottom: 1rem;
    font-size: 1.5rem;
    color: goldenrod;
    list-style: none; }

  .header-main a{color: snow;}
