*{
  box-sizing:border-box;
  margin: 0;
  padding: 0;}
html {
  font-size: 62.5%; /*usar 1rem para 10pixeles. 62.5% = 10px (en base al navegador)
  1rem = 10px
  1.6rem = 16px*/
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background:whitesmoke;}

/* ---------------------------------------------------------------------------- */

.card-main{
  display: block;
  margin: 1rem 2rem;
    /* width: auto; */
  /* text-align: left; */}

.card-main div {
  height: 35rem;
  /* display: none; */
  color: whitesmoke;}

.card-main h1 {
 font-size: 3rem;
  padding: 1rem;}

.card-main span {
  /* padding-block-start: 3rem; */
  -webkit-padding-start: 1rem;
          padding-inline-start: 1rem;
  }

  .card-main p {
    display: inline-block;
    margin: 30px 56px 8px 56px;
    max-width: 360px;
    max-height: 208px;
    text-align: justify;
    opacity: 0.3;
    font-size: small;
    vertical-align: top;}

  .coloresNat:hover p{
  opacity: 1;}
  .trabajoArt:hover p{
    opacity: 1;}
  .minimalist:hover p{
    opacity: 1;}

.carousel-tarj {
  margin: 2rem;}
  
.carousel-bill {
  margin: 1rem 2rem;}

.coloresNat {
  background-image: url(/pagecomercial/static/media/colorCuero.8a607b1e.jpeg);
  /* margin-top: 1rem; */
  background-repeat: no-repeat;}

.trabajoArt{
  background-image: url(/pagecomercial/static/media/aMano.cf8befba.jpg);
  margin-top: 1rem;
  /* margin: 3rem; */
  background-repeat: no-repeat;}

.minimalist{
  background-image: url(/pagecomercial/static/media/minimalist.5ed2d8f7.jpg);
  /* text-align-last: right; */
  margin-top: 1rem;
  background-repeat: no-repeat;}

  
/* ----------------------------------------------------------------------------------------------- */
@media only screen and (min-width: 538px) { 
  .card-main{
    display: -webkit-flex;
    display: flex;
    margin: 1rem 1rem;
    margin: 1rem 2rem;}
  
    .card-main p{margin: 0px 18px 8px 18px;}

    .trabajoArt{margin-top: 0px; 
      margin-left: 1rem;}
    .minimalist{margin-top: 0px;
    margin-left: 1rem;}

}
/* ----------------------------------------------------------------------------------- */
@media only screen and (min-width: 860px) {
  .card-main p{margin: 31px 35px 8px 35px;}
  .card-main div {height: 34rem;}
}

/* -------------------------------------------------------------------------------------------- */
@media only screen and (min-width: 900px) {
  .carousel-main{
    display: grid;
    grid-template-columns: 50% 50%;
    margin: 5px 0rem;
}
.carousel-bill {
  margin: 5px 2rem 5px 5px;
}
.carousel-tarj {
  margin: 5px 5px 5px 2rem;
}
}
